import { useEffect, useState } from "react";
import MyButton from "../myButton/MyButton";
import style from "./fetchDog.module.css";

export default function FetchDog() {
  //       api
  interface IDog {
    message: string;
    status: string;
  }

  //             ,    

  const [dog, setDog] = useState<IDog>({
    message: "",
    status: "",
  });

  //    fetch ,     api  state (         )
  const fetchDog = () => {
    fetch("https://dog.ceo/api/breeds/image/random")
      .then((res) => res.json())
      //       dog
      //   setDog   
      //          
      .then((data) => setDog(data));
  };

  useEffect(() => {
    //       
    fetchDog();
  }, []);

  return (
    <div className="all-container">
       <h3>Dog API</h3>
      {/*        */}
      {dog.message && (
        <>
          <img className={style.dogImg} src={dog.message} alt="dog" />
          <MyButton isPrimary={true} text="update dog" onClick={fetchDog} />
        </>
      )}
    </div>
  );
}
